import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { ActivityHomeworkService } from './activity-homework.service';


@Injectable({
  providedIn: 'root'
})
export class ActivityHomeworkItemResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private activityHomeworkService: ActivityHomeworkService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.activityHomeworkService.getHomeWorkActivityData();

    if(data == null){

      // Navigate to parent url
      this.router.navigateByUrl('/activity/classwork/list');

    }


    return;

  }

}

