import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ActivityHomeworkService {

  private homeWorkActivityData = new BehaviorSubject<any>(null);
  homeworkactivitydatacast = this.homeWorkActivityData.asObservable();

  private homeWorkActivityStudentData = new BehaviorSubject<any>(null);
  homeworkactivitystudentdatacast = this.homeWorkActivityStudentData.asObservable();

  constructor() { }

  getHomeWorkActivityData(){
    return this.homeWorkActivityData.value
  }

  setHomeWorkActivityData(value){
    this.homeWorkActivityData.next(value);
  }

  getHomeWorkActivityStudentData(){
    return this.homeWorkActivityStudentData.value
  }

  setHomeWorkActivityStudentData(value){
    this.homeWorkActivityStudentData.next(value);
  }

}
