import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityHomeworkItemResolver } from './activity-homework.resolver';

import { ActivityHomeworkComponent } from './activity-homework.component';

const routes: Routes = [
  { path: 'list', component: ActivityHomeworkComponent},
  { path: 'list/details/:id',
    loadChildren: () => import('src/app/modules/admin/activity/activity-homework/activity-homework-item/activity-homework-item.module').then(m => m.ActivityHomeworkItemModule),
    resolve: {
      ActivityHomeworkItemResolver
    }
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityHomeworkRoutingModule { }
